.pedido-container {
  padding: 20px;
  background: var(--container-background);
  color: var(--text-color);
  font-family: 'Arial', sans-serif;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.pedido-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.pedido-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--container-background);
  padding: 20px;
  border-radius: 10px;
  z-index: 1001;
  width: 80%;
  max-width: 500px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.pedido-modal-content {
  position: relative;
  background: var(--form-background);
  padding: 20px;
  border-radius: 10px;
}

.pedido-close {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 24px;
  color: var(--text-color);
  cursor: pointer;
}

.user-type-selection {
  margin-bottom: 20px;
  color: var(--text-color);
  display: flex;
  justify-content: space-around;
}

.user-type-selection button {
  padding: 10px 20px;
  background-color: var(--button-color);
  color: var(--button-text-color);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.user-type-selection button:hover {
  background-color: var(--button-hover-color);
}

.client-search {
  margin-bottom: 20px;
  color: var(--text-color);
}

.client-details {
  margin-top: 20px;
  color: var(--text-color);
}

.pedido-form {
  display: flex;
  flex-direction: column;
}

.pedido-form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.pedido-form-group label {
  margin-bottom: 5px;
  color: var(--text-color);
}

.pedido-form-group input {
  padding: 10px;
  border: 1px solid var(--input-border);
  border-radius: 5px;
  background: var(--input-background);
  color: var(--text-color);
}

.pedido-form-group select {
  padding: 10px;
  border: 1px solid var(--input-border);
  border-radius: 5px;
  background: var(--input-background);
  color: var(--text-color);
}

.pedido-list {
  margin-top: 20px;
}

.pedido-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border: 1px solid var(--input-border);
  border-radius: 5px;
  background: var(--input-background);
  color: var(--text-color);
  margin-bottom: 10px;
}

.pedido-item .button-container {
  display: flex;
  gap: 5px;
  margin-left: auto;
}

.pedido-item button {
  background-color: var(--button-color);
  color: var(--button-text-color);
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
  margin-left: 10px;
  height: 30px;
  width: 30px;
  transition: background-color 0.3s;
}

.pedido-item button:hover {
  background-color: var(--button-hover-color);
}

